	module.exports = {"data":{"markdownRemark":{"html":"<p>In order to hack on private programs, you must receive invitations to them. You can receive invitations if you meet the following criteria:</p>\n<ul>\n<li>Established reputation</li>\n<li>Non-negative signal</li>\n<li>Clear record with zero code of conduct violations</li>\n</ul>\n<p>When your Reputation, Signal, and Impact are high enough, you'll join the pool of hackers that receive access to private programs. Once you start receiving invitations to those programs, remember, the one universal rule is to not talk about the private programs you're a part of.</p>\n<p>HackerOne manages invitations for programs by:</p>\n<ul>\n<li>Daily checking to see if the program has met their report volume target in the last 30-days</li>\n<li>Inviting hackers for the program if they're not reaching their report volume target</li>\n</ul>\n<h3>How Invitations Work</h3>\n<p>The number of invitations HackerOne sends is based on the program's report volume on a rolling 30-day basis. These are the use cases as to how HackerOne sends invitations:</p>\n<table>\n<thead>\n<tr>\n<th>Case</th>\n<th>Action</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>The program is far from meeting their report volume.</td>\n<td>HackerOne will gradually increase the number of invitations so the program can meet their report volume.</td>\n</tr>\n<tr>\n<td>The program is receiving more reports while still being under their report volume.</td>\n<td>HackerOne will send out fewer invitations.</td>\n</tr>\n<tr>\n<td>The program's report volume is met.</td>\n<td>Invitations will pause until the volume drops below the target in the 30 day window.</td>\n</tr>\n<tr>\n<td>The program's report volume is set to 0.</td>\n<td>No invitations will be sent.</td>\n</tr>\n</tbody>\n</table>\n<p>Sometimes programs will manually invite you to hack on their programs. Keep in mind that it's a violation of Disclosure Guidelines for you to reach out to a private program in request for an invitation.</p>\n<h3>Invitation Expirations</h3>\n<p>Invitations to hack a program expire in 1 week. You may want to reject an invitation for one reason or another. The sooner you let HackerOne know that you're not going to accept the invitation, the sooner the invite can be sent to another hacker.</p>\n<h3>Invitation Preferences</h3>\n<p>Some hackers may want to get invites from any private program, while others only want invites from those offering bounties, and some may not want any invitations at all.</p>\n<p>To choose when to receive invitations to private programs, you can navigate to <strong>Settings > Invitation Preferences</strong>.</p>\n<p>You can choose from these options:</p>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Detail</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Always</td>\n<td>Receive invitations for every private program you're invited to.</td>\n</tr>\n<tr>\n<td>Only bug bounty programs</td>\n<td>Only receive invitations for private programs that award bounties.</td>\n</tr>\n<tr>\n<td>Never</td>\n<td>Exclude yourself from receiving invitations from any private program.</td>\n</tr>\n</tbody>\n</table>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/invitations-hackers-1-a0055ee159dea872f4aedaf5a93feea8-94acf.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 49.22279792746114%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"invitation-hackers-1\"\n        title=\"\"\n        src=\"/static/invitations-hackers-1-a0055ee159dea872f4aedaf5a93feea8-2f183.png\"\n        srcset=\"/static/invitations-hackers-1-a0055ee159dea872f4aedaf5a93feea8-67829.png 125w,\n/static/invitations-hackers-1-a0055ee159dea872f4aedaf5a93feea8-5c59f.png 250w,\n/static/invitations-hackers-1-a0055ee159dea872f4aedaf5a93feea8-2f183.png 500w,\n/static/invitations-hackers-1-a0055ee159dea872f4aedaf5a93feea8-c93e8.png 750w,\n/static/invitations-hackers-1-a0055ee159dea872f4aedaf5a93feea8-4e628.png 1000w,\n/static/invitations-hackers-1-a0055ee159dea872f4aedaf5a93feea8-b3ac4.png 1500w,\n/static/invitations-hackers-1-a0055ee159dea872f4aedaf5a93feea8-94acf.png 1930w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"title":"Invitations","id":"hackers/invitations","path":"/hackers/invitations.html","description":null}}},"pathContext":{}}

